<template>
  <div class="login-container">
    <header class="header">
      <div class="header-left">
        <span class="iconfont icon-fanhui"
              @click="$router.back()" />
        <span>登录</span>
      </div>
      <span class="header-right">密码登录</span>
    </header>
    <div class="login-info">
      <div class="phonenumber">
        <input type="number"
               name="phone"
               placeholder="手机号"
               v-model="phone" />
        <input type="button"
               id="btnGetVali"
               value="获取验证码" />
      </div>
      <input type="number"
             placeholder="验证码">
    </div>
    <div class="note">
      <p>温馨提示：未注册的手机号，登录时将自动注册，且代表您已同意
        <span>《用户服务协议》</span>
        <span>《隐私政策》</span>
      </p>
    </div>
    <div class="loginbtn">
      <button>登录</button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      phone: '15879170729'
    }
  },
  watch: {
    phone (newValue, oldValue) {
      let reg = /^((13|14|15|16|17|18|19)\d{9})$/
      if (reg.test(newValue)) {
        document.querySelector('#btnGetVali').style.backgroundColor = '#007aff'
        document.querySelector('#btnGetVali').disabled = false
      } else {
        document.querySelector('#btnGetVali').style.backgroundColor = 'rgba(0, 0, 0, 0.1)'
        document.querySelector('#btnGetVali').disabled = true
      }
    }
  },
  mounted () {
    let reg = /^((13|14|15|16|17|18|19)\d{9})$/
    if (!reg.test(this.phone)) {
      document.querySelector('#btnGetVali').style.backgroundColor = 'rgba(0, 0, 0, 0.1)'
      document.querySelector('#btnGetVali').disabled = true
    }
  }
}
</script>


<style lang="scss" scoped>
.login-container {
  height: 99.9vh;
  background-color: rgba(0, 0, 0, 0.1);
  .header {
    padding: 0 5px;
    display: flex;
    justify-content: space-between;
    background-color: #007aff;
    color: #fff;
    height: 55px;
    .header-left {
      display: flex;
      span {
        font-size: 16px;
        line-height: 55px;
        margin-left: 10px;
        margin-right: 10px;
      }
    }
    .header-right {
      line-height: 55px;
      font-size: 14px;
    }
  }
  .login-info {
    background-color: #fff;
    height: 80px;
    .phonenumber {
      position: relative;
      margin-top: 10px;
      height: 40px;
      input[name="phone"] {
        width: 100%;
        height: 100%;
        font-size: 14px;
        border: 0;
      }
      input[type="button"] {
        position: absolute;
        right: 5px;
        height: 34px;
        width: 90px;
        margin: 3px 0;
        padding: 0;
        color: #fff;
        font-size: 14px;
        border: 0;
        border-radius: 0;
        background-color: #007aff;
      }
    }
    input[type="number"] {
      height: 40px;
      font-size: 14px;
      border-radius: 0;
      border-left: 0;
      border-right: 0;
    }
  }
  .note {
    margin: 10px 8px 15px;
    p {
      font-size: 12px;
      color: black;
      span {
        color: #007aff;
      }
      span:hover {
        background-color: #ddf0ed;
      }
    }
  }
  .loginbtn {
    margin: 0 5px;
    button {
      height: 40px;
      width: 100%;
      padding: 0;
      font-size: 16px;
      border: 0;
      border-radius: 0;
      color: #fff;
      background-color: green;
    }
    button:hover {
      background-color: darkgreen;
    }
  }
}
</style>

